import React, { useEffect, useState } from 'react'
import './login.css'
import loginbg from '../Image/bgimg1.jpg'
import { useNavigate } from 'react-router-dom';
import { firebaseApp } from '../Firebase/fbConfig';
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";

function Login() {
    const [name, setName] = useState("");
    const [pass, setPass] = useState("");
    const [showpass, setshowPass] = useState(false);

    const navigate = useNavigate();
    const [loginflag, setloginflag] = useState(false);

    //login With FireBase
    const handleLogin = () => {
        const auth = getAuth(firebaseApp);
        console.log(auth);

        signInWithEmailAndPassword(auth, name, pass)
            .then((userCredential) => {

                const user = userCredential.user;
                console.log("kjlkopo", user);
                localStorage.setItem("Authorization", JSON.stringify(user.accessToken));
                localStorage.setItem("user", JSON.stringify(user));
                setloginflag(true);
                navigate("/");
                window.location.reload();

                console.log(user);
            })
            .catch((error) => {
                // const errorCode = error.code;
                // const errorMessage = error.message;
            });
    };

    useEffect(() => {
        if (loginflag) {
            navigate("/");
            console.log("kjlkopo", loginflag);
        }
    }, [loginflag]);


    return (
        <>
            <section
                className="LoginPage"
                style={{ backgroundImage: `url(${loginbg})` }}
            >
                <div className="LoginBgOverlay"></div>
                <div className="LoginForm">
                    <div className="LoginTop">
                        <h5 className="LoginHead">Login</h5>
                    </div>
                    <div className="LoginBtm">
                        <form action="">
                            <div className="form-group">
                                <input onChange={(e) => setName(e.target.value)} placeholder='Username' />
                            </div>

                            <div className="form-group pass_input">
                                <input
                                    type='password'
                                    onChange={(e) => setPass(e.target.value)}
                                    name="password" placeholder='Password' />
                                {/* <div
                                    className="eye_icon"
                                    onClick={() => {
                                        setshowPass(!showpass);
                                    }}
                                > */}
                                {/* <i className="fa-solid fa-eye"></i> */}
                                {/* </div> */}

                            </div>

                            <div className="form-group">
                                <input type="Checkbox" placeholder='Password' />
                                <span className="LoginRem">Remember Me</span>
                            </div>
                        </form>

                        <button
                            onClick={() => handleLogin()}
                            className="LoginBtn"
                            type="submit"
                        >
                            SIGN IN
                        </button>
                    </div>
                </div >
            </section >
        </>
    )
}

export default Login
