import React, { useContext, useEffect, useState } from "react";
import "./booking.css";
import { Link, useNavigate } from "react-router-dom";
import TopCartbox from "../../Component/TopCartbox";
import { POSITIONCONTEXT } from "../../Context/PositionContext";
import { get, getDatabase, set, ref as RTRef, child } from "firebase/database";
import toast from "react-hot-toast";
import { v4 as uuidv4 } from 'uuid';
import moment from "moment";

function Booking() {

  const value = useContext(POSITIONCONTEXT)


  const initial = {
    noOFpassenger: "",
    noOfbags: "",
    firstname: "",
    lastname: "",
    email: "",

    phno: "",
    opinion: "",
  }
  const [destination, setdestination] = useState(initial);
  const [duration, setduration] = useState(initial);
  console.log("kjkj", duration)
  const [fromdata, setfromData] = useState(initial);


  const userData = JSON.parse(localStorage.getItem("user"))




  const getuserDetails = async () => {
    const dbRef = RTRef(getDatabase());
    const snapshot = await get(child(dbRef, 'users/' + userData.uid));
    if (snapshot.exists()) {
      const userDatas = snapshot.val();

      setfromData({

        firstname: userDatas.firstName,
        lastname: userDatas.lastname,
        email: userDatas?.email,

        phno: userDatas.phno,

      })
    }

  }
  useEffect(() => {
    getuserDetails()
  }, [])



  const onChange = (e) => {
    const { name, value } = e.target

    setfromData((prevData) => ({
      ...prevData,
      [name]: value,
    }));



  }

  const navigate = useNavigate()

  //sending Driver data in realTime Database
  function writeUserData() {

    const db = getDatabase();



    if (value.startPoint.lat && value.endPoint.lat) {

      console.log("lehjdg", value)

      const data = {


        startinglatitude: value.startPoint.lat,
        startinglongitude: value.startPoint.lng,
        starting: value.startAddress,

        endinglatitude: value.endPoint.lat,
        endinglongitude: value.endPoint.lng,
        ending: value.endAddress,
        createdAt: moment(new Date()).format("YYYY-MM-DDTHH:mm:ssZ"),
        time: value.time,
        distance: destination,
        bookingTime: duration,
        bookingDateTime: moment(new Date(value?.date)).format("YYYY-MM-DDTHH:mm:ssZ"),

        status: "Upcoming",
        userId: userData.uid,

      }



      set(RTRef(db, 'UserBookings/Upcomming/' + userData.uid + '/' + uuidv4()), data)
        .then((res) => {

          toast.success("DriverBooking  created successfully!");
          console.log(data, "kk")
          const newdata = { ...data, duration: duration, time: destination }


          navigate("/checkout", { state: { data: newdata } })
          value.setStartAddress("")
          value.setStartPoint("")
          value.setendAddress("")
          value.setendPoint("")
          value.settime("")
          value.setendAddress("")


        })
        .catch((error) => {

          console.error("Error writing user data:", error);
        });

    } else {
      toast.error("All fields are required ")
    }
  }


  const getAllLocationDatas = () => {

    const apikey = "AIzaSyAMkH3YDYb_ZJJvtuuEl_Grwc6u9U_yuSM"
    const directionsServiceBaseUrl = 'https://maps.googleapis.com/maps/api/directions/json';

    const url = `${directionsServiceBaseUrl}?origin=${value.startPoint.lat},${value.startPoint.lng}&destination=${value.endPoint.lat},${value.endPoint.lng}&key=${apikey}&mode=driving&language=English`;

    console.log("url", url)
    fetch(url)
      .then(response => response.json())
      .then(json => {

        // if (json.status !== 'OK') {
        //     const errorMessage = json.error_message || json.status || 'Unknown error';
        //     return Promise.reject(errorMessage);
        // }

        console.log("ppwqo", json.routes[0].legs[0]);
        if (json.routes.length) {

          const route = json.routes[0];

          const decodepkey = route.overview_polyline
          // const decodepolyCode = decode([{ polyline: decodepkey }])
          // setStreetView(decodepolyCode)
          setdestination(json.routes[0].legs[0].distance.text)
          setduration(json.routes[0].legs[0].duration.text)

          console.log(json.routes[0].legs[0])
        }
      }).catch((err) => {
        console.log(err)
        // alert(err)
      })
  }



  useEffect(() => {
    getAllLocationDatas()
  }, [])

  return (
    <>
      <section className="header2"></section>
      <TopCartbox />
      <section className="bradecomeSection">
        <div className="custContain">
          <Link to="/">
            <i class="fa-solid fa-angle-left"></i> Home
          </Link>
          <Link to="/Booking" className="active">
            {" "}
            <i class="fa-solid fa-angles-right"></i> Reservation From
          </Link>
        </div>
      </section>

      <section className="bookingFromSection">
        <div className="custContain">
          <div className="rgsForm">
            <div className="">
              <form>
                <div className="Bookingname">
                  <div className="inputname">
                    <label>Number Of Passengers</label>
                    <input onChange={onChange} value={fromdata.noOFpassenger} name="noOFpassenger" type="number" placeholder="0" />

                    {/* <select name="noOFpassenger" value={fromdata.noOFpassenger}>
                      <option>05</option>
                      <option>06</option>
                      <option>07</option>
                      <option>08</option>
                    </select> */}
                  </div>
                  <div className="inputname">
                    <label>Number Of Bags</label>
                    <input onChange={onChange} value={fromdata.noOfbags} name="noOfbags" type="number" placeholder="0" />

                    {/* <select name="noOfbags" value={fromdata.noOfbags}>
                      <option>05</option>
                      <option>06</option>
                      <option>07</option>
                      <option>08</option>
                    </select> */}
                  </div>
                </div>

                <div className="Bookingname">
                  <div className="inputname">
                    <label>First Name</label>
                    <input onChange={onChange} value={fromdata.firstname} name="firstname" type="text" placeholder="John" />
                  </div>
                  <div className="inputname">
                    <label>Last Name</label>
                    <input onChange={onChange} value={fromdata.lastname} name="lastname" type="text" placeholder="Doe" />
                  </div>
                </div>
                <div className="Bookingname">
                  <div className="inputname">
                    <label>Email Address</label>
                    <input onChange={onChange} value={fromdata.email} name="email" type="email" placeholder="email@gmail.com" />
                  </div>

                  <div className="inputname">
                    <label>Phone Number</label>
                    <input onChange={onChange} value={fromdata.phno} name="phno" type="number" placeholder="+ 4921479654" />
                  </div>
                </div>
                <div className="commentSection">
                  <label>Additional Information</label>
                  <textarea onChange={onChange} value={fromdata.opinion} name="opinion" type="comment" placeholder="Lorem Ipsum"></textarea>
                </div>
                <div className="bookingcontinu">
                  <Link >
                    <button className="BookingcontinuBtn" onClick={() => { writeUserData() }}>
                      Continue <i class="fa-solid fa-arrow-right-long"></i>
                    </button>
                  </Link>
                </div>
              </form>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}

export default Booking;
